<template>
  <v-col cols="12" sm="6" md="4" lg="3">
    <v-card raised class="task" :class="setClass" @click="togglePending">
      <v-system-bar color="rgba(0, 0, 0, 0.2)" dark>
        <v-spacer></v-spacer>
        <v-btn x-small icon @click.stop="deleteTask(id)">
          <v-icon color="white">mdi-delete-forever</v-icon>
        </v-btn>
      </v-system-bar>
      <v-card-title class="justify-center text-center white--text">{{ task.name }}</v-card-title>
      <v-card-subtitle class="text-center white--text">{{ state }}</v-card-subtitle>
    </v-card>
  </v-col>
</template>

<script>
export default {
  props: {
    task: { type: Object, required: true },
    id: { type: Number, required: true }
  },
  computed: {
    setClass() {
      return {
        pending: this.task.pending,
        done: !this.task.pending
      };
    },
    state() {
      return this.task.pending ? "Pendente" : "Concluída";
    }
  },
  methods: {
    togglePending() {
      this.task.pending = !this.task.pending;
      this.$store.dispatch("saveTasks");
    },
    deleteTask(i) {
      this.$store.dispatch("deleteTask", i);
      this.$store.dispatch("saveTasks");
    }
  }
};
</script>

<style>
div.task {
  cursor: pointer;
  user-select: none;
}
div.task.pending {
  background-color: #ef5350;
}
div.task.done {
  background-color: #9ccc65;
}
</style>