import Vue from "vue";
import Vuex from "vuex";
import firebase from "firebase";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    user: {
      uid: null,
      name: null,
    },
    tasks: [],
    status: "",
    error: "",
    loading: false,
  },
  getters: {
    getUser(state) {
      return state.user;
    },
    getUserUid(state) {
      return state.user.uid;
    },
    getTasks(state) {
      return state.tasks;
    },
    getPercentTasks(state) {
      const total = state.tasks.length;
      const done = state.tasks.filter((t) => !t.pending).length;
      return Math.round((done / total) * 100) || 0;
    },
    getLoading(state) {
      return state.loading;
    },
  },
  mutations: {
    setUser(state, payload) {
      state.user.uid = payload.uid;
      state.user.name = payload.name;
    },
    setUserUid(state, payload) {
      state.user.uid = payload;
    },
    resetUser(state, payload) {
      state.user.uid = payload.uid;
      state.user.name = payload.name;
    },
    addTask(state, payload) {
      state.tasks.push(payload);
    },
    deleteTask(state, payload) {
      state.tasks.splice(payload, 1);
    },
    setTasks(state, payload) {
      state.tasks = payload;
    },
    setStatus(state, payload) {
      state.status = payload;
    },
    setError(state, payload) {
      state.error = payload;
    },
    setLoading(state, payload) {
      state.loading = payload;
    },
  },
  actions: {
    initStore({ commit, getters, dispatch }) {
      if (getters.getUserUid == null) {
        let uid = firebase.auth().currentUser.uid;

        commit("setLoading", true);

        firebase
          .database()
          .ref("/users/" + uid)
          .once("value")
          .then((snapshot) => {
            commit("setUser", { uid: uid, name: snapshot.val().name });
            dispatch("setTasks", snapshot.val().tasks);
            commit("setLoading", false);
          })
          .catch(() => {
            commit("setLoading", false);
          });
      } else {
        commit("setLoading", false);
      }
    },
    addTask({ commit, state }, payload) {
      const sameName = (t) => t.name === payload;
      const reallyNew = state.tasks.filter(sameName).length == 0;

      if (reallyNew) {
        let task = {
          name: payload,
          pending: true,
        };
        commit("addTask", task);
      }
    },
    deleteTask({ commit }, index) {
      commit("deleteTask", index);
    },
    setTasks({ commit }, payload) {
      payload =
        payload != undefined || payload != null ? Object.values(payload) : [];
      commit("setTasks", payload);
    },
    saveTasks({ getters }) {
      let tasks = Object.assign(getters.getTasks);
      firebase
        .database()
        .ref("/users/" + getters.getUserUid + "/tasks")
        .set(tasks);
    },
    signUp({ commit }, payload) {
      commit("setLoading", true);

      return firebase
        .auth()
        .createUserWithEmailAndPassword(payload.email, payload.pass)
        .then(
          (user) => {
            const userId = user.user.uid;

            const data = {
              name: payload.name,
              timestamp: firebase.database.ServerValue.TIMESTAMP,
              tasks: {},
            };

            commit("setUser", {
              uid: userId,
              name: payload.name,
            });

            firebase
              .database()
              .ref("users/" + userId)
              .set(data);
          },
          (err) => {
            commit("setLoading", false);
            alert("Aconteceu algo inesperado. " + err.message);
          }
        );
    },
    signInAction({ commit, dispatch }, payload) {
      commit("setLoading", true);

      return firebase
        .auth()
        .signInWithEmailAndPassword(payload.email, payload.password)
        .then((user) => {
          return firebase
            .database()
            .ref("/users/" + user.user.uid)
            .once("value")
            .then((snapshot) => {
              commit("setUser", {
                uid: user.user.uid,
                name: snapshot.val().name,
              });
              dispatch("setTasks", snapshot.val().tasks);
            });
        })
        .catch((error) => {
          commit("setStatus", "failure");
          commit("setError", error.message);
          commit("setLoading", false);
        });
    },
    logoutAction({ commit }) {
      commit("setLoading", true);

      return firebase
        .auth()
        .signOut()
        .then(() => {
          let userReset = {
            uid: null,
            name: null,
          };
          commit("resetUser", userReset);
          commit("setLoading", false);
        });
    },
  },
});
