<template>
  <v-container>
    <v-row align="center" justify="center">
      <v-col cols="12" sm="8" md="6">
        <v-card light>
          <v-card-text>
            <v-form ref="form">
              <v-text-field
                class="mb-2"
                outlined
                label="Nome"
                hint="Digite seu nome completo"
                :rules="nameRule"
                v-model="name"
                prepend-inner-icon="mdi-account"
              ></v-text-field>
              <v-text-field
                class="mb-2"
                outlined
                label="E-mail"
                hint="Digite seu endereço de e-mail"
                :rules="emailRule"
                v-model="email"
                prepend-inner-icon="mdi-at"
              ></v-text-field>
              <v-text-field
                class="mb-2"
                outlined
                :append-icon="showPass1 ? 'mdi-eye' : 'mdi-eye-off'"
                :rules="passRule"
                :type="showPass1 ? 'text' : 'password'"
                prepend-inner-icon="mdi-key-variant"
                label="Senha"
                hint="Crie uma senha de 6 digitos"
                counter
                v-model="pass1"
                @click:append="showPass1 = !showPass1"
              ></v-text-field>
              <v-text-field
                class="mb-2"
                outlined
                :append-icon="showPass2 ? 'mdi-eye' : 'mdi-eye-off'"
                :rules="[confirmPass]"
                :type="showPass2 ? 'text' : 'password'"
                prepend-inner-icon="mdi-key-variant"
                label="Confirmar senha"
                hint="Repita a senha"
                counter
                v-model="pass2"
                @click:append="showPass2 = !showPass2"
              ></v-text-field>
            </v-form>
            <v-btn block x-large color="success" @click.prevent="confirm">
              Registrar
              <v-icon right>mdi-account-plus</v-icon>
            </v-btn>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
export default {
  data: () => ({
    showPass1: false,
    showPass2: false,
    name: "",
    email: "",
    pass1: "",
    pass2: "",
    nameRule: [v => !!v || "Campo nome não preenchido"],
    emailRule: [
      v => !!v || "Campo e-mail não preenchido",
      v => /.+@.+/.test(v) || "E-mail inválido"
    ],
    passRule: [v => v.length >= 6 || "A senha deve conter 6 digitos"]
  }),
  computed: {
    confirmPass() {
      return () => this.pass1 === this.pass2 || "Senhas diferentes";
    }
  },
  methods: {
    confirm() {
      if (this.$refs.form.validate()) {
        this.$store
          .dispatch("signUp", {
            name: this.name,
            email: this.email,
            pass: this.pass1
          })
          .then(() => {
            this.$router.push("/home");
          });
      } else {
        console.log("falho");
      }
    }
  }
};
</script>

