<template>
  <v-content>
    <v-app-bar>
      <v-app-bar-nav-icon></v-app-bar-nav-icon>
      <v-spacer></v-spacer>
      <v-icon left>mdi-alarm</v-icon>
      <v-toolbar-title>Tarefas</v-toolbar-title>

      <v-spacer></v-spacer>

      <v-btn icon @click="toggleTheme">
        <v-icon>mdi-brightness-6</v-icon>
      </v-btn>

      <v-menu left bottom>
        <template v-slot:activator="{ on }">
          <v-btn icon v-on="on">
            <v-icon>mdi-dots-vertical</v-icon>
          </v-btn>
        </template>

        <v-list>
          <v-list-item @click="() => {}">
            <v-list-item-title @click="logout">
              <v-icon right>mdi-logout</v-icon>Logout
            </v-list-item-title>
          </v-list-item>
        </v-list>
      </v-menu>
    </v-app-bar>
    <v-container>
      <new-task>
        <v-progress-linear height="6" slot="progressbar" bottom color="info" :value="progressTasks"></v-progress-linear>
      </new-task>
      <tasks-grid :progressTasks="progressTasks" />
    </v-container>
    <v-dialog v-model="dialog" width="500">
      <v-card>
        <v-img
          class="align-center mb-6"
          src="https://i.pinimg.com/originals/ac/55/bb/ac55bb99d22f13bb3d9ee9fa8d7b186a.gif"
          height="120px"
        >
          <v-card-title class="justify-center headline white--text" primary-title>
            Seja bem vindo(a)
            <v-icon right>em-smiling_face_with_3_hearts</v-icon>
          </v-card-title>
        </v-img>

        <v-card-text>
          {{ user.name }}, esse aplicativo web tem o simples propósito em te ajudar a gerenciar suas tarefas diárias,
          com ele você pode adicionar sua lista de tarefas do dia, como por exemplo, lavar a louça, recolher o lixo,
          limpar o quarto e entre outras tarefas do dia. Apos criar uma tarefa, clicando em cima dela, você marca a mesma como concluída.
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="danger" text @click="logout">Sair</v-btn>
          <v-btn color="success" text @click="dialog = false">Entrar</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-content>
</template>

<script>
import newTask from "@/components/newTask.vue";
import tasksGrid from "@/components/tasksGrid.vue";

export default {
  components: { newTask, tasksGrid },
  data: () => ({
    dialog: false
  }),
  computed: {
    progressTasks() {
      return this.$store.getters.getPercentTasks;
    },
    loading() {
      return this.$store.getters.getLoading;
    },
    user() {
      return this.$store.getters.getUser;
    }
  },
  watch: {
    loading(v) {
      this.dialog = v ? false : true;
    }
  },
  methods: {
    logout() {
      this.$store.dispatch("logoutAction").then(() => {
        this.$router.push("/login");
      });
    },
    toggleTheme() {
      this.$vuetify.theme.dark = !this.$vuetify.theme.dark;
    }
  },
  created() {
    this.$store.dispatch("initStore");
  }
};
</script>

