<template>
  <div id="tasksGrid" v-if="!loading">
    <template v-if="tasks.length > 0">
      <p class="body-1 text-center">
        Você está com
        <span class="font-weight-bold primary--text">{{ progressTasks }}%</span> das tarefas concluidas.
      </p>
      <v-row justify="center">
        <task v-for="(task, i) in tasks" :key="task.name" :task="task" :id="i"></task>
      </v-row>
    </template>
    <template v-else>
      <p class="text-center">
        Parabéns, você não possui tarefas
        <v-icon right>em-sunglasses</v-icon>
        <v-icon right>em-stuck_out_tongue_winking_eye</v-icon>
      </p>
    </template>
  </div>
</template>

<script>
import task from "@/components/task.vue";

export default {
  components: { task },
  props: {
    progressTasks: { type: Number, required: false }
  },
  computed: {
    tasks() {
      return this.$store.getters.getTasks;
    },
    loading() {
      return this.$store.getters.getLoading;
    }
  }
};
</script>

