<template>
  <v-container>
    <v-row align="center" justify="center">
      <v-col cols="12" sm="8" md="6">
        <v-card light>
          <v-card-text>
            <v-form ref="form" lazy-validation>
              <v-text-field
                class="mb-2"
                outlined
                label="E-mail"
                :rules="emailRules"
                v-model="email"
                prepend-inner-icon="mdi-at"
              ></v-text-field>
              <v-text-field
                class="mb-2"
                outlined
                :append-icon="showPass ? 'mdi-eye' : 'mdi-eye-off'"
                :rules="passRules"
                :type="showPass ? 'text' : 'password'"
                prepend-inner-icon="mdi-key-variant"
                label="Senha"
                counter
                v-model="pass"
                @click:append="showPass = !showPass"
              ></v-text-field>
            </v-form>
            <v-btn class="mb-2" block x-large color="primary" @click="validation">
              Logar
              <v-icon right>mdi-login</v-icon>
            </v-btn>
            <v-btn block x-large color="success" to="/registrar">
              Registrar
              <v-icon right>mdi-account-plus</v-icon>
            </v-btn>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
export default {
  data: () => ({
    email: "",
    pass: "",
    showPass: false,
    emailRules: [
      v => !!v || "Preencha o e-mail",
      v => /.+@.+/.test(v) || "E-mail inválido"
    ],
    passRules: [
      v => !!v || "Preencha a senha",
      v => v.length >= 6 || "A senha deve conter 6 digitos"
    ]
  }),
  methods: {
    validation() {
      if (this.$refs.form.validate()) {
        this.signIn();
      } else console.log("Formulario invalido");
    },
    signIn() {
      this.$store
        .dispatch("signInAction", {
          email: this.email,
          password: this.pass
        })
        .then(() => {
          this.$router.push("/home");
        });
    }
  }
};
</script>

