<template>
  <v-card raised tile class="mt-8 mb-8">
    <v-row no-gutters>
      <v-col cols="12" sm="9" md="10">
        <v-text-field
          v-model="taskName"
          flat
          solo
          hide-details
          height="48"
          placeholder="Tarefa"
          style="border-radius: 0"
          @keydown.enter="addTask"
        ></v-text-field>
      </v-col>
      <v-col cols="12" sm="3" md="2">
        <v-btn
          large
          depressed
          tile
          block
          height="48"
          color="info"
          @click="addTask"
        >
          adicionar
          <v-icon right>mdi-plus-thick</v-icon>
        </v-btn>
      </v-col>
      <v-col cols="12">
        <slot name="progressbar"></slot>
      </v-col>
    </v-row>
  </v-card>
</template>

<script>
export default {
  data: () => ({
    taskName: ""
  }),
  methods: {
    addTask() {
      if (this.taskName != "" && this.taskName != null) {
        this.$store.dispatch("addTask", this.taskName);
        this.$store.dispatch("saveTasks");
        this.taskName = "";
      }
    }
  }
};
</script>

